:root {
  --border0: #ff0707;
  --border1: #ff6a07;
  --border2: #ffc507;
  --border3: #7fff07;
  --border4: #07ff8b;
  --border5: #072cff;


  --h1: #230C00;
  --ciudades: #180200;
  --grados: #230C00;

    --sm: 320px;
    --md: 375px;
    --lg: 425px;
    --xl: 1200px;
    --xxl: 1400px;

}



.main {
  background: rgb(86 79 62 / 25%);
  border-radius: 2em;
  margin-top: -14em; /* cambio reciente desmdare -200*/
  position: absolute;
  display:flex;
  justify-content: center;
}

.cmain {
  bottom: 5em;
  position: relative;
}

/* Interior */
.App {
  /*border: 5px solid var(--border1);
  */
  /* border-radius: 1.5em; */
  /*height:150px;
  */
  bottom: -90px;
  position:relative;
  height:0px; /*500 100*/
}


/* CELULAR */
.Appcontainer {
   display:grid;
   place-items: center;
   width:20em; /* 20em*/
   height: 98vh;
   border-radius: 2rem;
   background:linear-gradient(rgba(170, 155, 117, .2) 0%, rgba(203,177,116, .2) ), url("https://p.favim.com/orig/2018/10/24/sky-cloud-yellow-Favim.com-6488572.jpg");
   background-size: cover;
   box-shadow: -20px 48px 91px -11px rgba(0,0,0,0.75);
   -webkit-box-shadow: -20px 48px 91px -11px rgba(0,0,0,0.75);
   -moz-box-shadow: -20px 48px 91px -11px rgba(0,0,0,0.75);

}


.busqueda {
  top: -15em;
  left: 40px;
  position: absolute;

  /*border: 5px solid var(--border1);*/
     /*background-clip: padding-box;*/
   /*border: solid 5px transparent;*/
    
}



